.urlsArrayItemRoot{
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: flex-start;
  flex-wrap: wrap;
}
.urlsArrayItemWrapper{
  width: 30px;
  height: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 10px;
  margin-bottom: 10px;
  border-radius: 100%;
  border: 1px solid #000;
  cursor: pointer;
}
.urlsArrayItemWrapper:hover{
  background-color: #f0f0f0;
}
.urlsArrayItemText{
  font-size: 14px;
  font-family: Arial, sans-serif;
}
